@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
body{margin:0;text-align:center;background-color:#f5f4fa;font-family:'Varela Round', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}ul{margin:0;padding:0;list-style:none}a{text-decoration:none}img{display:block;max-width:100%;height:auto}h1,h2,h3,h4{margin:0;padding:0}p{margin:0}

.App{text-align:center}.Header{display:flex;justify-content:center;padding:30px;background-color:#363945;background-position:center;background-size:cover;box-shadow:0px 1px 3px rgba(0,0,0,0.12),0px 1px 1px rgba(0,0,0,0.14),0px 2px 1px rgba(0,0,0,0.2)}.NavList{display:flex}.NavLink{font-size:2.1em}.NavLink:first-child{margin-right:30px}.Return-button{position:absolute;top:29%;left:4%;display:inline-block;padding:3px 10px;border-radius:4px;background-color:#7c9fec;color:#fff}.Movies-container{text-align:center}

